.animated-object {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 2000;
  pointer-events: none;
  will-change: transform;
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
}

.tech-logo {
  position: absolute;
  width: 120px;
  height: 120px;
  will-change: transform, opacity, filter, left, top;
  z-index: 3000;
  pointer-events: none;
  /* Initial position will be set by JavaScript */
}

.logo-container {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none;
}

.tech-circle {
  position: relative;
  width: 60px;
  height: 60px;
  background: linear-gradient(135deg, #6366f1 0%, #a855f7 50%, #22c55e 100%);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 
    0 0 20px rgba(99, 102, 241, 0.6),
    inset 0 0 20px rgba(255, 255, 255, 0.2);
  animation: tech-pulse 2s ease-in-out infinite;
  z-index: 15;
}

@keyframes tech-pulse {
  0%, 100% {
    transform: scale(1);
    box-shadow: 
      0 0 20px rgba(99, 102, 241, 0.6),
      inset 0 0 20px rgba(255, 255, 255, 0.2);
  }
  50% {
    transform: scale(1.1);
    box-shadow: 
      0 0 30px rgba(99, 102, 241, 0.8),
      inset 0 0 30px rgba(255, 255, 255, 0.3);
  }
}

.circle-inner {
  width: 45px;
  height: 45px;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(10px);
}

.tech-symbol {
  color: white;
  font-size: 1.2rem;
  font-weight: 700;
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
}

.tech-rings {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
}

.ring {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 2px solid;
  border-radius: 50%;
  animation: ring-rotate 6s linear infinite;
}

.ring-1 {
  width: 90px;
  height: 90px;
  border-color: rgba(99, 102, 241, 0.7);
  animation-duration: 4s;
}

.ring-2 {
  width: 120px;
  height: 120px;
  border-color: rgba(168, 85, 247, 0.6);
  animation-duration: 6s;
  animation-direction: reverse;
}

.ring-3 {
  width: 150px;
  height: 150px;
  border-color: rgba(34, 197, 94, 0.5);
  animation-duration: 8s;
}

@keyframes ring-rotate {
  0% {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  100% {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}

.orbit-dots {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 120px;
  height: 120px;
}

.orbit-dot {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 6px;
  height: 6px;
  background: radial-gradient(circle, #6366f1, #a855f7);
  border-radius: 50%;
  transform-origin: 0 60px;
  animation: orbit-rotate 4s linear infinite;
  box-shadow: 0 0 10px rgba(99, 102, 241, 0.8);
}

@keyframes orbit-rotate {
  0% {
    transform: rotate(0deg) translateX(60px) rotate(0deg);
  }
  100% {
    transform: rotate(360deg) translateX(60px) rotate(-360deg);
  }
}

.tech-lines {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
}

.line {
  position: absolute;
  background: linear-gradient(90deg, transparent, #6366f1, transparent);
  height: 2px;
  width: 40px;
  top: 50%;
  left: 50%;
  transform-origin: center;
  animation: line-pulse 2s ease-in-out infinite;
}

.line-1 {
  transform: translate(-50%, -50%) rotate(0deg);
  animation-delay: 0s;
}

.line-2 {
  transform: translate(-50%, -50%) rotate(45deg);
  animation-delay: 0.5s;
}

.line-3 {
  transform: translate(-50%, -50%) rotate(90deg);
  animation-delay: 1s;
}

.line-4 {
  transform: translate(-50%, -50%) rotate(135deg);
  animation-delay: 1.5s;
}

@keyframes line-pulse {
  0%, 100% {
    opacity: 0.3;
    transform: translate(-50%, -50%) scale(1);
  }
  50% {
    opacity: 1;
    transform: translate(-50%, -50%) scale(1.2);
  }
}

/* Responsive design */
@media (max-width: 768px) {
  .tech-logo {
    width: 100px;
    height: 100px;
    /* Position will be set by JavaScript */
  }
  
  .tech-circle {
    width: 50px;
    height: 50px;
  }
  
  .circle-inner {
    width: 35px;
    height: 35px;
  }
  
  .tech-symbol {
    font-size: 1rem;
  }
  
  .ring-1 {
    width: 70px;
    height: 70px;
  }
  
  .ring-2 {
    width: 90px;
    height: 90px;
  }
  
  .ring-3 {
    width: 110px;
    height: 110px;
  }
  
  .orbit-dots {
    width: 90px;
    height: 90px;
  }
  
  .orbit-dot {
    width: 5px;
    height: 5px;
    transform-origin: 0 45px;
  }
  
  .line {
    width: 30px;
    height: 1px;
  }
}

@media (max-width: 480px) {
  .tech-logo {
    width: 80px;
    height: 80px;
    /* Position will be set by JavaScript */
  }
  
  .tech-circle {
    width: 40px;
    height: 40px;
  }
  
  .circle-inner {
    width: 30px;
    height: 30px;
  }
  
  .tech-symbol {
    font-size: 0.9rem;
  }
  
  .ring-1 {
    width: 60px;
    height: 60px;
  }
  
  .ring-2 {
    width: 75px;
    height: 75px;
  }
  
  .ring-3 {
    width: 90px;
    height: 90px;
  }
  
  .orbit-dots {
    width: 70px;
    height: 70px;
  }
  
  .orbit-dot {
    width: 4px;
    height: 4px;
    transform-origin: 0 35px;
  }
  
  .line {
    width: 25px;
    height: 1px;
  }
}
