.App {
  text-align: left;
  position: relative;
  overflow-x: hidden;
  cursor: none;
}


/* Custom Cursor */
.custom-cursor {
  position: fixed;
  width: 20px;
  height: 20px;
  background: radial-gradient(circle, rgba(99, 102, 241, 0.8), transparent);
  border-radius: 50%;
  pointer-events: none;
  z-index: 9998;
  transform: translate(-50%, -50%);
  transition: all 0.1s ease;
  mix-blend-mode: difference;
}

.cursor-follower {
  position: fixed;
  width: 40px;
  height: 40px;
  border: 2px solid rgba(99, 102, 241, 0.3);
  border-radius: 50%;
  pointer-events: none;
  z-index: 9997;
  transform: translate(-50%, -50%);
  transition: all 0.2s ease;
  backdrop-filter: blur(5px);
}

/* Global section styles */
.section {
  min-height: 100vh;
  padding: 0 5%;
  display: flex;
  align-items: center;
  position: relative;
  z-index: 1;
}

.section-content {
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
}

/* Enhanced scroll effects */
.section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, transparent 30%, rgba(99, 102, 241, 0.01) 50%, transparent 70%);
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
}

.section:hover::before {
  opacity: 1;
}

/* Smooth transitions for all interactive elements */
* {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Enhanced focus states */
*:focus {
  outline: 2px solid rgba(99, 102, 241, 0.5);
  outline-offset: 2px;
}

/* Responsive design */
@media (max-width: 768px) {
  .section {
    padding: 0 4%;
  }
  
  .custom-cursor,
  .cursor-follower {
    display: none;
  }
  
  .App {
    cursor: auto;
  }
}

@media (max-width: 480px) {
  .section {
    padding: 0 3%;
  }
}
